import React, { Component } from 'react';
import { Button, Div } from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';
import './style.css';

import Icon16Gift from '@vkontakte/icons/dist/16/gift';
import Icon24FavoriteOutline from '@vkontakte/icons/dist/24/favorite_outline';
import Icon24Place from '@vkontakte/icons/dist/24/place';
import Icon24Recent from '@vkontakte/icons/dist/24/recent';

import ListCardGift from '../ListCardGift';

export default class Event extends Component {
    constructor(props) {
        super(props);

        this.state = {
            gifts: [],
        };
    }

    componentDidMount() {
        fetch('http://127.0.0.1:8000/get-tasks/', {
            method: 'POST',
            body: JSON.stringify({ id_concert: this.props.data[0].id }),
        })
            .then(res => res.json())
            .then(res => {
                this.setState({ gifts: res.RESPONSE });

                return res;
            })
            .catch(res => console.log(Error(res)));
    }

    render() {
        const event = this.props.data[0];
        const imageUrl = event.subevents[0].image
            ? 'http://media.cultserv.ru/i/300x200/' + event.subevents[0].image
            : 'data:image/jpeg;base64,';
        const date = new Date(event.subevents[0].date);
        const months = [
            'января',
            'февраля',
            'марта',
            'апреля',
            'мая',
            'июня',
            'июля',
            'августа',
            'сентября',
            'октября',
            'ноября',
            'декабря',
        ];
        const beautyDate =
            date.getDate() +
            ' ' +
            months[date.getMonth()] +
            ' в ' +
            (date.getHours() < 9 ? '0' : '') +
            date.getHours() +
            ':' +
            (date.getMinutes() < 9 ? '0' : '') +
            date.getMinutes();

        return (
            <div className="event_page__wrap">
                <div className="event_page__poster_wrap">
                    <div
                        className="event_page__image"
                        style={{ backgroundImage: 'url(' + imageUrl + ')' }}
                    />
                    <div className="event_page__fade" />
                    <div className="event_page__info">
                        <div className="event_page__title">{event.title}</div>
                        <div className="event_page__info_item_wrap">
                            <Icon24Recent />
                            {beautyDate}
                        </div>
                        <div className="event_page__info_item_wrap">
                            <Icon24Place />
                            {event.subevents[0].venue.address}
                        </div>
                        <Button
                            size="l"
                            className="event_page__more mx-auto"
                            level="outline"
                            component="a"
                            href={event.seo.link}
                        >
                            Подробнее на ponominalu.ru
                        </Button>
                    </div>
                </div>
                <Div>
                    <Button
                        className="event_page__gift_btn"
                        size="l"
                        before={<Icon16Gift />}
                        level="secondary"
                    >
                        Вы можете получить
                    </Button>
                </Div>
                <ListCardGift
                    gifts={this.state.gifts}
                    onGiftClick={this.props.onGiftClick}
                />
            </div>
        );
    }
}
